import Vue from 'vue'
import Router from 'vue-router'
import BolgIndex from '@/components/BolgIndex'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      name: 'BolgIndex',
      component: BolgIndex
    }
  ]
})



