/* eslint-disable */
// eslint-disable-next-line
// eslint-disable-next-line no-use-before-define
<template>
  <div>
      <div class="columns">
          <div class="column is-4 is-offset-1">
                <section class="section is-primary">
                    <div class="hero-body">
                        <h1 class="title">When you are old </h1>
                        <h2>
                            <vue-typer 
                                text="
When you are old and grey and full of sleep. 
And nodding by the fire, take down this book. 
And slowly read, and dream of the soft look 
Your eyes had once, and of their shadows deep. 
How many loved your moments of glad grace. 
And loved your beauty with love false or true. 
But one man loved the pilgrim soul in you. 
And loved the sorrows of your changing face; 
And bending down beside the glowing bars, 
Murmur, a little sadly, how Love fled 
And paced upon the mountains overhead 
And hid his face amid a crowd of stars."
                                repeat=0    
                                typeDelay=10
                                @completed='onComplete'></vue-typer>
                        </h2>
                    </div>
                </section>

          </div>

          
          <div class="column is-4">
              <section class="section is-primary">
                <div calss="hero-body">
                    <!-- <img src="/static/imgs/laole.PNG" /> -->
                    <img src="/static/imgs/manli.jpg"/>
                </div>
              </section>
          </div>


          <div class = "column is-2 ">
              <section class="section is-primary">
                <div class="rows" v-show="isShow">
                    <div class="row is-6 is-offset-5">
                            <a class="button is-primary myButton" @click="enterBolg">进入博客</a>
                    </div>
                </div>
              </section>
      </div>
          
      </div>

      
  </div>
</template>

<script type="text/ecmascript-6">
  import {VueTyper} from "vue-typer";

  export default {
    data() {
      return {
          isShow:false,
          p5obj: null
      }

    },
    
    methods:{
        onComplete(){
            this.isShow = true;
        },
        enterBolg(){
            window.location.href="content/archives"
        },
        onP5Created(obj) {
            this.p5obj = obj;
        }
    },

    components: {
        VueTyper
    },

    mounted(){
        const script = function (p5) {
            var speed = 2;    
            var posX = 0;
            var location = p5.createVector(0,0);
            var location2 = p5.createVector(0,0);
            // var location3 = p5.createVector(0,0);
            // var location4 = p5.createVector(0,0);
            var mouse = p5.createVector(0,0);
            
            // NOTE: Set up is here   
            p5.setup = _ => {      
                p5.createCanvas(window.innerWidth, 300);      
                p5.ellipse(p5.width / 2, p5.height / 2, 500, 500);    
            }     

            // NOTE: Draw is here
            p5.draw = _ => {
                mouse = p5.createVector(p5.mouseX,p5.mouseY);
                p5.background(30,144,255);
                const degree = p5.frameCount * 3;      
                const y = p5.sin(p5.radians(degree)) * 50;
                    
                p5.push();
                p5.translate(0, p5.height / 2);
                p5.fill(124);
                p5.ellipse(posX, y, 50, 50);
                p5.pop();
                posX += speed;
                    
                if (posX > p5.width || posX < 0) {    
                    speed *= -1;      
                };
                p5.fill(0);

                location.add(mouse.sub(location).normalize().mult(7));
                p5.ellipse(location.x,location.y,20,20);
            }  
        }

        // NOTE: Use p5 as an instance mode
        const P5 = require('p5');
        new P5(script);
    }
  }
</script>

<style>
  @import "bulma/css/bulma.css";

  .myButton{
      width: 100px;
      height: 60px;
  }
  
</style>
/*eslint-enable */


